/* === Base Styles === */
.app-root {
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  position: relative;
}

/* === Search Bar === */
.search-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  position: absolute;
  top: 12rem;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.3s ease-in-out;
}

.search-form {
  display: flex;
  align-items: center;
}

.search-input {
  height: 3.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0 1.5rem;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 1rem;
}

.search-input:focus {
  opacity: 0.8;
  outline: none;
}

/* === Hide/Search Toggle Button === */
.hide-search-button {
  height: 3.5rem;
  font-size: 1rem;
  font-weight: 600;
  padding: -1 -0rem;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
  display: flex;
  align-items: center;
}

.hide-search-button:hover {
  background: rgba(86, 86, 86, 0.2);
}

/* Inline when search visible */
.hide-search-button.search-visible {
  height: 3.5rem;
  position: absolute;
  margin-left: 130%;
  margin-top: -5%;
}

/* Top-left when search hidden */
.hide-search-button.search-hidden {
  position: absolute;
  top: -10rem;
  left: -77rem;
}

/* === Widget Grid === */
.widgets {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 600px));
  justify-content: center;
  gap: 10rem;
  width: 100%;
  padding: 2rem;
  box-sizing: border-box;
  align-content: center;
}
.widget {
  width: 100%;
  min-height: 620px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* === Now Playing === */
.now-playing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  min-height: 620px;
  transition: all 0.3s ease-in-out;
}

.album-art-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 10px;
}

.album-art-bg {
  width: 600px;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  will-change: transform;
}

.album-art {
  width: 100%;
  height: 100%;
  min-height: 300px;
  object-fit: cover;
  border-radius: 20px;
}

.track-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
  width: 600px;
  margin: 10px 0 0 0;
}

.track-name {
  font-size: 2.5rem;
  font-weight: bold;
  color: rgba(188, 188, 188, 0.8);
  margin: 10px 0;
}

.track-artists {
  font-size: 1.7rem;
  font-weight: normal;
  color: rgba(188, 188, 188, 0.8);
  margin: 0;
}

.nothing-playing {
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 600;
  color: #9a9a9a;
  text-align: center;
  padding: 0.75rem 1.5rem;
  border-radius: 1rem;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border: 0px solid rgba(255, 255, 255, 0);
  margin: 0 auto;
}

/* === Calendar Widget Container === */
.calendar-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

/* === Utility === */
.hidden {
  display: none !important;
}

.login-button {
  display: block;
  font-size: 1rem;
  font-weight: 600;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  padding: 1rem 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  max-width: 30%;
  margin: 15rem auto;
  text-align: center;
}

/* === To-Do List === */
.todo-container {
  width: 100%;
  max-width: 600px;
  height: 620px;                /* matches calendar */
  overflow-y: auto;
  padding: 1rem 2rem;
  background: rgba(86,86,86,0.1);
  backdrop-filter: blur(10px);
  border-radius: 1rem;
  box-shadow: 0 4px 30px rgba(0,0,0,0.2);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.todo-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: rgba(255, 255, 255, 0.85);
}

.todo-input {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border-radius: 0.75rem;
  border: none;
  outline: none;
  margin-bottom: 1rem;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  flex: 1;
  height: 3rem;           /* pick whatever height you like */
  padding: 0 1rem;        /* horizontal padding only */
  margin-bottom: 0;       /* remove extra bottom margin */
  box-sizing: border-box; /* include padding in the height */
}

.todo-button {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 0.75rem;
  background-color: rgba(255, 255, 255, 0.15);
  color: #fff;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.todo-button:hover {
  background-color: rgba(255, 255, 255, 0.25);
}

.todo-list {
  list-style: none;
  padding: 0;
  margin-top: 1rem;
  flex: 1;
  overflow-y: auto;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  padding: 0.5rem 1rem;
  border-radius: 0.75rem;
  margin-top: 0.5rem;
  word-break: break-word;
  max-width: 100%;
}

.todo-item button {
  background: none;
  border: none;
  color: #f87171;
  font-weight: bold;
  cursor: pointer;
}

.todo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.todo-toggle-btn {
  background: rgba(86, 86, 86, 0.2);
  color: #9a9a9a;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s ease;
}

.todo-toggle-btn:hover {
  background: rgba(86, 86, 86, 0.3);
}

.todo-widget-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 100%;
  max-width: 600px;
}
